.Container {
  margin: auto;
  padding: 5px;
  width: 640px;
  border: 1px solid black;
}

.InputKeys {
  display: grid;
  grid-template: repeat(1fr, 5)/repeat(1fr, 4);
  height: 800px;
  width: 640px;
}

.Display {
  height: 160px;
  width: 640px;
  border-bottom: 3px solid black;
  margin: 3px;
  text-align: right;
  text-justify: center;
  font-size: 3em;
}

.button {
  border-radius: 20%;
  background-color: hsl(0, 0%, 90%);
  font-size: 2em;
}
.button:hover{
  background-color: hsl(0, 0%, 100%)
}

#clear {
  grid-column: 1 / span 2;
  grid-row: 1 / span 1;
  background-color: hsl(5, 93%, 72%)
}

#add {
  grid-column: 3 / span 1;
  grid-row: 1 / span 1;
  background-color: hsl(36, 98%, 58%);
}

#subtract {
  grid-column: 4 / span 1;
  grid-row: 1 / span 1;
  background-color: hsl(36, 98%, 58%);
}

#multiply {
  grid-column: 4 / span 1;
  grid-row: 2 / span 1;
  background-color: hsl(36, 98%, 58%);
}

#divide {
  grid-column: 4 / span 1;
  grid-row: 3 / span 1;
  background-color: hsl(36, 98%, 58%);
}

#equals {
  grid-column: 4 / span 1;
  grid-row: 4 / span 2;
  background-color: hsl(36, 98%, 58%);
}

#one {
  grid-column: 1 / span 1;
  grid-row: 2 / span 1;
}

#two {
  grid-column: 2 / span 1;
  grid-row: 2 / span 1;
}

#three {
  grid-column: 3 / span 1;
  grid-row: 2 / span 1;
}

#four {
  grid-column: 1 / span 1;
  grid-row: 3 / span 1;
}

#five {
  grid-column: 2 / span 1;
  grid-row: 3 / span 1;
}

#six {
  grid-column: 3 / span 1;
  grid-row: 3 / span 1;
}

#seven {
  grid-column: 1 / span 1;
  grid-row: 4 / span 1;
}

#eight {
  grid-column: 2 / span 1;
  grid-row: 4 / span 1;
}

#nine {
  grid-column: 3 / span 1;
  grid-row: 4 / span 1;
}

#zero {
  grid-column: 1 / span 2;
  grid-row: 5 / span 1;
}

#decimal {
  grid-column: 3 / span 1;
  grid-row: 5 / span 1;
}